*{
    margin: 0px;
    padding: 0px;
}
.containerGeneral{
    min-width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: row-reverse;
}
.containerDesktop{
    display: none;
}
.containerMobil{
    //background-image: url('../img/fondo.png');
    background-color: #000;
    background-size: cover;
    min-width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: space-around;
    .logo{
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        max-height: 85vw;
        img{
            min-width: 30vw;
            max-width: 30vw;
        }
    }
    .titulo{
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        max-height: 85vw;
        img{
            min-width: 50vw;
            max-width: 5vw;
        }
    }
    .formulario{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        max-height: 85vw;
        min-height: 50vh;
        flex-wrap: wrap;
        .filtro{
            //z-index: 4;
            z-index: 1;
            position: absolute;
            min-width: 100vw;
            min-height: 100vh;
            margin-bottom: 50vh;
            background-color: rgba(0,0,0,0.5);
            //background-color: aqua;
            display: flex;
        }
        
        .contenedor{
            z-index: 2;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            input{
                margin: 0px;
                padding: 0px;
                min-width: 85vw;
                max-height: 85vw;
                padding-left: 0vw;
                min-height: 40px;
                line-height: 2px;
                justify-content: center;
                border: none;
                background-color: transparent;
                color: #999999;
                line-height: 0px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.5);
                line-height: 0px;
                padding: 0px;
                font-family: Corporative;
                font-weight: 600;
                font-size: 18px;
                font-style: normal;
                
            }
            input::placeholder{
                color: #999999;
                line-height: 0px;
                padding: 0px;
                min-height: 40px;
                display: flex;
                flex-wrap: wrap;
                font-family: Corporative;
                vertical-align: text-bottom;
                
                font-size: 25px;
                
            }
            .triangulo{
                position: absolute;
                img{
                    min-width: 8px;
                    max-width: 8px;
                }
            }
        }
       
        .botom{
            z-index: 2;
            margin: 0px;
            padding: 0px;
            border: none;
            min-width: 85vw;
            max-height: 85vw;
            display: flex;
            padding-left: 0vw;
            min-height: 40px;
            font-size: 20px;
            line-height: 2px;
            justify-content: left;
            align-items: center;
            text-decoration: none;
            background-color: transparent;
            border-bottom: 1px solid #E30C26;
            color: #E30C26;
            font-family: Corporative;
            font-size: 25px;
            font-weight: 600;
        }
        
    }
    .footer{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        min-width: 85vw;
        max-height: 85vw;
        .tituloFooter{
            min-width: 85vw;
            max-height: 85vw;
            color: #FFF;
            text-align: center;
            font-size: 20px;
            font-family: Alfarn;
            text-transform: initial;

            img{
                min-width: 85vw;
                max-width: 85vw;
            }

        }
        .numeroFooter{        
            min-width: 85vw;
            max-height: 85vw;
            color: #E30C26;
            font-weight: 600;
            font-family: Alfarn;
            font-size: 20px;
            text-align: center;
            
            img{
                min-width: 65vw;
                max-width: 65vw;
            }

        }
    }
    .messageFooter{
        border: 1px solid red;
        min-width: 100vw;
        min-height: 25vh;
        background-color: #E80024;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        display: none;
        .titulo{
            color: #FFF;
            font-family: Corporative;
            font-size: 50px;
            font-weight: 600;
        }
        .texto{
            color: #FFF;
            font-family: Corporative;
            font-size: 30px;
            font-weight: 600;
        }

    }
    .logoFooter{
        min-height: 10vh;
        background-color: #FFF;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 100vw;
        max-height: 100vw;
        img{
            min-width: 50vw;            
            max-width: 50vw;

        }
    }
}






